.skills {
  display: flex; }
.partDefault {
  display: grid;
  grid-template-columns: 7fr 5fr;
  align-items: center;
  grid-column-gap: 200px;
  &.partTwo {
    margin-top: 100px; } }
.partImg  img {
  width: 100%; }
.partImgReverse  img {
  width: 100%; }
.wrap {
  padding: 100px 0;
  background-color: #fff;
  overflow: hidden; }
.partText p {
  margin: 60px 0; }
.partLeft {
  justify-self: flex-end;
  text-align: right; }
.title {
  position: relative;
  &:after {
    content: '知識';
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-size: 100px;
    font-weight: 500;
    opacity: .6; }
  &.titleReverse {
    &:after {
      left: auto;
      right: 20px;
      content: '私自身'; } } }
