@import url('https://fonts.googleapis.com/css?family=Josefin+Sans&display=swap');
@import 'common.css';
body {
  font-family: 'Josefin Sans', sans-serif; }
.container {
  padding: 40px 60px;
  box-sizing: border-box; }
.appWrap {
  position: relative;
  overflow: hidden;
  width: 100%;
  box-sizing: border-box; }
.textDefault {
  font-size: 24px;
  line-height: 36px;
  color: #545454;
  p {
    max-width: 550px; } }
.titleDefault {
  font-size: 42px;
  display: block;
  width: 100%;
  color: #CE3F1F; }
.hieroglyph {
  font-weight: 500;
  padding: 60px 0;
  display: inline-block;
  transform: translateX(-100%);
  font-size: 100px; }
.description {
  display: grid;
  grid-column-gap: 100px;
  padding-left: 200px;
  grid-template-columns: 6fr 6fr; }
@keyframes rotate {
  0% {
    transform: rotate(0); }
  100% {
    transform: rotate(360deg); } }
@media screen and (max-width: 1200px) {
  .container {
    padding: 20px 40px; } }
@media screen and (max-width: 480px) {
  .container {
    padding: 10px 11px; } }
